import React, { Component } from 'react'
import styled from 'styled-components'

import './styles/fonts.css'
import Jumbo from './components/Jumbo'
import TopPoints from './components/TopPoints'
import MarketOpportunity from './components/MarketOpportunity'
import Solutions from './components/Solutions'
import Subscribe from './components/Subscribe'
import WhyBlockchain from './components/WhyBlockchain'
import Partners from './components/Partners'
import PartnersCTA from './components/PartnersCTA'
import Team from './components/Team'
import NewsAndUpdates from './components/NewsAndUpdates'
import Contact from './components/Contact'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showContactForm: false
    }
  }

  toggleContactForm = () => {
    this.setState({
      showContactForm: !this.state.showContactForm
    })
  }

  render() {
    return (
      <>
        <Main>
          <Jumbo
            showContactForm={this.state.showContactForm}
            toggleContactForm={this.toggleContactForm}
          />
          <TopPoints />
          <MarketOpportunity />
          <Solutions />
          <Subscribe />
          <WhyBlockchain />
          <Partners />
          <PartnersCTA />
          <Team />
        </Main>
        <NewsAndUpdates />
        <Contact toggleContactForm={this.toggleContactForm} />
      </>
    )
  }
}

export default App

const Main = styled.div`
  padding: 0 10%;
`
