import React, { Component } from 'react'
import styled from 'styled-components'

class Contact extends Component {
  constructor(props) {
    super(props)
    this.state = {
      email: '',
      subscribed: false
    }
  }

  validateEmail = (email) => {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(String(email).toLowerCase())
  }

  subscribe = () => {
    if (this.validateEmail(this.state.email)) {
      fetch(
        'https://us-central1-norblock-c98d8.cloudfunctions.net/contactForm_cannatrac?email=' +
          this.state.email
      ).then(function(res) {
        console.log(res)
      })
      this.setState({
        subscribed: true,
        email: ''
      })

      setTimeout(
        function() {
          this.setState({
            subscribed: false
          })
        }.bind(this),
        1000
      )
    } else {
      alert('Invalid email address.')
    }
  }

  handleChange = (e) => {
    this.setState({
      email: e.target.value
    })
  }

  render() {
    return (
      <>
        <Main id="bottom">
          <Left>
            <BtmMenu>
              <Item href="#home">Home</Item>
              <Item href="#opportunity">Opportunity</Item>
              <Item href="#solution">Solutions</Item>
              <Item href="#technology">Technology</Item>
              <Item href="#team">Team</Item>
              <Item
                target="_blank"
                href="https://medium.com/northern-block/cannatrac/home"
              >
                Blog
              </Item>
            </BtmMenu>
          </Left>

          <Right>
            <Heading>Subscribe to our newsletter for updates</Heading>
            <InputsButton>
              {/* <Input placeholder="Name" /> */}
              <Input
                placeholder="E-mail"
                value={this.state.email}
                onChange={this.handleChange}
              />
              <SubBtn onClick={this.subscribe}>
                {this.state.subscribed ? 'Subscribed!' : 'Subscribe'}
              </SubBtn>
            </InputsButton>
          </Right>
        </Main>
        <BtmLine> Copyright 2019 Cannatrac Inc. All Rights Reserved.</BtmLine>
      </>
    )
  }
}

export default Contact

