import React, { Component } from 'react'
import styled from 'styled-components'

class ContactForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      email: '',
      name: '',
      message: '',
      subscribed: false
    }
  }

  validateEmail = (email) => {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(String(email).toLowerCase())
  }

  submit = () => {
    if (this.validateEmail(this.state.email)) {
      if (this.state.name === '') {
        alert('Please enter your name')
      } else if (this.state.message === '') {
        alert('Please enter a valid message')
      } else {
        console.log(this.state)

        fetch(
          'https://us-central1-norblock-c98d8.cloudfunctions.net/largeContactForm_cannatrac',
          {
            method: 'POST',
            headers: {
              Accept: 'application/json',
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.state)
          }
        ).then((res) => {
          console.log(res)
        })

        this.setState({
          subscribed: true,
          email: '',
          name: '',
          message: ''
        })

        setTimeout(
          function() {
            this.setState({
              subscribed: false
            })
            this.props.closeForm()
          }.bind(this),
          1500
        )
      }
    } else {
      alert('Invalid email address.')
    }
  }

  handleChange = (e, name) => {
    this.setState({
      [name]: e.target.value
    })
  }

  render() {
    return (
      <Outer>
        <Inner>
          <BgImg src={require('../assets/contactForm-bg-logo.png')} />

          <Cancel onClick={this.props.closeForm}>
            <CancelImg src={require('../assets/mobileWidgetBtn-close.png')} />
          </Cancel>

          <HeadingWhite>Send Us A Message</HeadingWhite>

          <Input
            placeholder="Email"
            onChange={(e) => this.handleChange(e, 'email')}
            value={this.state.email}
          />
          <Input
            placeholder="Full Name"
            onChange={(e) => this.handleChange(e, 'name')}
            value={this.state.name}
          />
          <Textarea
            placeholder="Message"
            onChange={(e) => this.handleChange(e, 'message')}
            value={this.state.message}
          />
          <CTABtn onClick={this.submit}>
            {this.state.subscribed ? 'Sent!' : 'Submit'}
          </CTABtn>
          <OR>or</OR>
          <CTABtn2 href="https://calendly.com/cannatrac/30min" target="_blank">
            Schedule a Call
          </CTABtn2>
        </Inner>
      </Outer>
    )
  }
}

export default ContactForm

