import React, { Component } from 'react'
import styled from 'styled-components'

import ContactForm from './ContactForm'

class Jumbo extends Component {
  constructor(props) {
    super(props)
    this.state = {
      scrollytop: false,
      mobileMenu: false
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', (e) => {
      if (window.scrollY > 0) {
        this.setState({
          scrollytop: true
        })
      } else {
        this.setState({
          scrollytop: false
        })
      }
    })
  }

  toggleMobileMenu = () => {
    this.setState({
      mobileMenu: !this.state.mobileMenu
    })
  }

  toggleContactForm = () => {
    this.setState({
      mobileMenu: !this.state.mobileMenu
    })
    this.props.toggleContactForm()
  }

  render() {
    return (
      <>
        <Main id="home">
          <JumboImg src={require('../assets/jumbo.png')} />

          <Header header={this.state.scrollytop}>
            <Logo src={require('../assets/logo.png')} />

            <Menu>
              <Item header={this.state.scrollytop} href="#home">
                Home
              </Item>
              <Item header={this.state.scrollytop} href="#opportunity">
                Opportunity
              </Item>
              <Item header={this.state.scrollytop} href="#solution">
                Solutions
              </Item>
              <Item header={this.state.scrollytop} href="#technology">
                Technology
              </Item>
              <Item header={this.state.scrollytop} href="#team">
                Team
              </Item>
              <Item
                header={this.state.scrollytop}
                target="_blank"
                href="https://medium.com/northern-block/cannatrac/home"
              >
                Blog
              </Item>
              <WhiteItem
                header={this.state.scrollytop}
                onClick={this.props.toggleContactForm}
              >
                Contact Us
              </WhiteItem>
            </Menu>
          </Header>

          <MobileHeader header={this.state.scrollytop}>
            <Logo src={require('../assets/logo.png')} />

            <BurgerMenu
              onClick={this.toggleMobileMenu}
              src={require('../assets/burgerMenu.png')}
            />
          </MobileHeader>

          {this.state.mobileMenu ? (
            <MobileMenu>
              <CloseBtn onClick={this.toggleMobileMenu}>
                <CloseImg src={require('../assets/close.png')} />
                <CloseText>Close</CloseText>
              </CloseBtn>

              <MobileLogo src={require('../assets/white-logo.png')} />

              <MobileMenuItems>
                <MobileMenuItem href="#home" onClick={this.toggleMobileMenu}>
                  Home
                </MobileMenuItem>
                <MobileMenuItem
                  href="#opportunity"
                  onClick={this.toggleMobileMenu}
                >
                  Opportunity
                </MobileMenuItem>
                <MobileMenuItem
                  href="#solution"
                  onClick={this.toggleMobileMenu}
                >
                  Solutions
                </MobileMenuItem>
                <MobileMenuItem
                  href="#technology"
                  onClick={this.toggleMobileMenu}
                >
                  Technology
                </MobileMenuItem>
                <MobileMenuItem href="#team" onClick={this.toggleMobileMenu}>
                  Team
                </MobileMenuItem>
                <MobileMenuItem href="#bottom" onClick={this.toggleMobileMenu}>
                  Blog
                </MobileMenuItem>
                <MobileMenuItem href="#bottom" onClick={this.toggleContactForm}>
                  Contact
                </MobileMenuItem>
              </MobileMenuItems>
            </MobileMenu>
          ) : null}

          <SplitSection>
            <Left>
              <Text1>Cannabis Profitability</Text1>
              <Text1>Powered By Blockchain</Text1>
              <Text2>
                Cannatrac is dedicated to setting the cannabis industry up for
                longer-term profitability through better compliance and brand
                management enabled by a complete and verifiable chain of custody
                on the blockchain.
              </Text2>
              <DemoImgMobile src={require('../assets/demoImg.png')} />
              <CTABtns>
                <CTABtn
                  href="https://calendly.com/cannatrac/30min"
                  target="_blank"
                >
                  Schedule a demo today
                </CTABtn>
                <CTABtn2
                  href="https://calendly.com/cannatrac/30min"
                  target="_blank"
                >
                  Discuss partnership
                </CTABtn2>
              </CTABtns>
            </Left>
            <Right>
              <DemoImg src={require('../assets/demoImg.png')} />
            </Right>
          </SplitSection>
        </Main>

        {this.props.showContactForm ? (
          <ContactForm closeForm={this.props.toggleContactForm} />
        ) : null}
      </>
    )
  }
}

export default Jumbo

const Main = styled.div`
  padding-top: 100px;
  min-height: 100vh;
`

const JumboImg = styled.img`
  width: 68%;
  position: absolute;
  z-index: -10;
  top: 0;
  right: 0;

  @media (min-width: 700px) and (max-width: 1250px) {
    width: 70%;
    /* opacity: 0.7; */
  }

  @media (max-width: 700px) {
    opacity: 0.7;
  }
`

const Header = styled.div`
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 80%;
  padding: 0 10%;
  background-color: ${({ header }) => (header ? 'white' : 'transparent')};
  /* background-color: purple; */
  font-family: 'futura', sans-serif;

  @media (max-width: 1250px) {
    display: none;
  }
`

